import { catalogMenu } from "@/data/catalogMenu";
import { Container } from "../container";
import {
  NavigationMenu,
  NavigationMenuContent,
  NavigationMenuItem,
  NavigationMenuLink,
  NavigationMenuList,
} from "../ui/navigation-menu";
import { NavigationMenuTrigger } from "@radix-ui/react-navigation-menu";

const CatalogMenu = () => {
  return (
    <div className="bg-gray-500 fixed w-full z-50">
      <Container>
        <div className="w-full bg-white h-screen overflow-y-auto">
          <NavigationMenu>
            <NavigationMenuList className="flex flex-col justify-start items-start">
              {catalogMenu.map((item, parentIndex) => (
                <NavigationMenuItem
                  key={`parent-${parentIndex}`}
                  className="relative group"
                >
                  {/* Parent link */}
                  <NavigationMenuTrigger className="w-[300px] text-start hover:bg-red-400 px-4 py-2">
                    {item.name}
                  </NavigationMenuTrigger>

                  {/* Submenu content */}
                  <NavigationMenuContent className="absolute left-full top-0 w-[300px] bg-gray-100 p-4 hidden group-hover:flex">
                    <ul>
                      {item.subLinks.map((subLink, subIndex) => (
                        <li
                          key={`sub-${parentIndex}-${subIndex}`}
                          className="mb-2"
                        >
                          <NavigationMenuLink
                            href={subLink.link}
                            className="block text-black hover:underline"
                          >
                            {subLink.name}
                          </NavigationMenuLink>
                        </li>
                      ))}
                    </ul>
                  </NavigationMenuContent>
                </NavigationMenuItem>
              ))}
            </NavigationMenuList>
          </NavigationMenu>
        </div>
      </Container>
    </div>
  );
};

export default CatalogMenu;
