"use client";
import { CatalogIcon } from "@/assets/icons";
import { Container } from "../container";
import { Button } from "../ui/button";
import CatalogMenu from "../catalog-menu/CatalogMenu";
import { useState } from "react";

const Navbar = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  return (
    <>
      <nav className="bg-white py-2">
        <Container>
          {/* catalog */}
          <div>
            <Button
              onClick={() => setIsMenuOpen(!isMenuOpen)}
              variant="outline"
              className="bg-cerulean w-[191px] h-10 flex items-center gap-[11px] text-white font-semibold text-sm leading-[18px] rounded-[9px] border-none hover:text-cerulean duration-300 ease-out"
            >
              <CatalogIcon />
              Каталог товаров
            </Button>
          </div>
        </Container>
      </nav>
      {isMenuOpen && <CatalogMenu />}
    </>
  );
};

export default Navbar;
